import { IconButton, IStackTokens, Stack } from "@fluentui/react";
import * as React from "react";
import { ConnectionStringConnector } from "../../Components/DataConnector/ConnectionStringConnector";
import { XrmConnectionString } from "../../Helper/Model";
import { getClassNames } from "./SolutionViewer.classNames";

interface SolutionViewerState {
    AllConnections: XrmConnectionString[],
    EditConnection?: XrmConnectionString
}

const verticalGapStackTokens: IStackTokens = {
    childrenGap: 20,
    padding: 10,
};

export class SolutionViewer extends React.Component<{}, SolutionViewerState> {
    connector = new ConnectionStringConnector();
    constructor({ }) {
        super({});
        this.state = {
            AllConnections: []
        }
    }

    async componentDidMount() {
        const connections = await this.connector.GetAll();
        this.setState({ AllConnections: connections });
    }
    render() {
        return <Stack
            horizontal
            verticalFill
            wrap
            horizontalAlign={"start"}
            verticalAlign={"start"}
            tokens={verticalGapStackTokens}>
            {this.state.AllConnections.map(e => <ConnectionStringCard {...e} />)}
            <IconButton iconProps={{ iconName: "Add" }} title="Add" ariaLabel="Add" onClick={() => this.AddNewConnectionClick()} />
        </Stack>
    }

    AddNewConnectionClick() {

    }

}

export const ConnectionStringCard: React.FC<XrmConnectionString> = (props) => {
    const style = getClassNames();
    return (
        <div className={style.cardtoPick} /*onClick={() => props.onClickActive(props)}*/>
            <div className={style.container}>
                <Stack horizontal horizontalAlign="space-between">
                    {props.Name} - {props.OrganizationUrl}
                </Stack>
            </div>
        </ div>
    )
}