import * as React from "react";
import * as ReactDOM from 'react-dom';
import { FontWeights, getTheme, initializeIcons, mergeStyleSets, Modal } from '@fluentui/react';
import { Collapser } from "./Components/Collapser";
import { SolutionViewer } from "./Pages/SolutionViewer/SolutionViewer";

interface IApp {
    InError: boolean;
    Error?: Error;
    ErrorInfo?: React.ErrorInfo;
}
initializeIcons();
const theme = getTheme();
export const modalStyles = mergeStyleSets({
    container: {
        display: 'flex',
        flexFlow: 'column nowrap',
        alignItems: 'stretch',
        width: '70%'
    },
    header: [
        theme.fonts.xLargePlus,
        {
            flex: '1 1 auto',
            borderTop: `4px solid ${theme.palette.themePrimary}`,
            color: theme.palette.neutralPrimary,
            display: 'flex',
            alignItems: 'center',
            fontWeight: FontWeights.semibold,
            padding: '12px 12px 14px 24px',
        },
    ],
    body: {
        flex: '4 4 auto',
        padding: '0 24px 24px 24px',
        overflowY: 'hidden',
        selectors: {
            p: { margin: '14px 0' },
            'p:first-child': { marginTop: 0 },
            'p:last-child': { marginBottom: 0 },
        },
    },
});

class App extends React.Component<{}, IApp> {

    constructor(props: any) {
        super(props);
        this.state = {
            InError: false
        };
    }

    public render() {
        if (!this.state.InError) {
            return <SolutionViewer />
        }
        return (
            <div>
                <Modal
                    isOpen={this.state.InError}
                    isBlocking={true}
                    containerClassName={modalStyles.container}
                >
                    <div className={modalStyles.header}>
                        <span>Errore</span>
                    </div>
                    <div className={modalStyles.body}>
                        <Collapser label="dettagli errore" children={<> <p>{this.state.Error?.message}</p>
                            <p>{this.state.ErrorInfo?.componentStack}</p></>} />
                    </div>
                </Modal>
            </div>
        );
    }

    public componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
        this.setState({
            InError: true,
            Error: error,
            ErrorInfo: errorInfo
        });
    }
}

ReactDOM.render(<App />, document.getElementById('root'));